.div-center {
    display: table;
    margin: 0 auto;
}

.activeViewCell {
    background-color: var(--button_secondary_background);
    border-radius: 8px;
}

.ProfileUserWeb {
    display: flex;
    margin: -7px -7px 0 -7px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 32px;
}

.ProfileUserMobail {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 32px;
}

.NameUser {
    margin-bottom: 8px; 
    margin-top: 20px
}

.SubheaderUser {
    margin-bottom: 24px;
    color: var(--text_secondary)
}

.icon {
    margin-left: 10px;
}

.scroll {
    width: available !important;
    overflow-x: auto !important;
    word-wrap: normal;
    -ms-user-select: auto;
    -moz-user-select: auto;
    -webkit-user-select: auto;
    user-select: auto;
}

.copy {
    -ms-user-select: auto;
    -moz-user-select: auto;
    -webkit-user-select: auto;
    user-select: auto;
}